        </tr>
        <tr v-for="(p,i) in Object.keys(people)" :key="i">
          <td>{{p}}</td>
          <td v-for="(s,j) in people[p]" :key="j">
            <button class="session-btn" :class="[s === 0 ? '' : 'session-selected']" @click="selectUnselectSession(p,j)">{{s === 0 ? '' : 'X'}}</button>
          </td>
          <td>
            {{calculateTotalHoursForPerson(p)}}
          </td>
        </tr>
      </table>
    </div>
    <div class="conditions-container">
      <h3>Conditions</h3>
      <div v-for="(p, i) in Object.keys(conditions)" :key="i" class="condition-container">
        <h5>{{p}}</h5>
        <div>
          <!-- <div class="number-condition-container">
            Minimum hours x week:
            <div>
              <button @click="increasDecreas('decreas', conditions[p], 'minHours')">-</button>
              <span>{{conditions[p].minHours}}</span>
              <button @click="increasDecreas('increas', conditions[p], 'minHours')">+</button>
            </div>
          </div> -->
          <div class="number-condition-container">
            Maximum hours x week:
            <div>
              <button @click="increasDecreas('decreas', conditions[p], 'maxHours')">-</button>
              <span>{{conditions[p].maxHours}}</span>
              <button @click="increasDecreas('increas', conditions[p], 'maxHours')">+</button>
            </div>
          </div>
          <div class="number-condition-container">
            Maximum courses to teach x week:
            <div>
              <button @click="increasDecreas('decreas', conditions[p], 'maxCourses')">-</button>
              <span>{{conditions[p].maxCourses}}</span>
              <button @click="increasDecreas('increas', conditions[p], 'maxCourses')">+</button>
            </div>
          </div>
          <!-- <div class="checkbox-condition-container">
            Required courses:
            <div>
              <div v-for="(c,j) in Object.keys(conditions[p].requiredCourses)" :key="j">
                <input type="checkbox" :id="p+c" v-model="conditions[p].requiredCourses[c]">
                <label :for="p+c">{{c + (c === "EP" ? "" : "ème")}}</label>
              </div>
            </div>
          </div> -->
          <div class="checkbox-condition-container">
            Forbidden courses:
            <div>
              <div v-for="(c,j) in Object.keys(conditions[p].forbiddenCourses)" :key="j">
                <input type="checkbox" :id="p+c" v-model="conditions[p].forbiddenCourses[c]">
                <label :for="p+c">{{c + (c === "EP" ? "" : "ème")}}</label>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="EP-consideration-container">
        <h5>Consider EP to calculate max courses?</h5>
        <div>
          <input type="checkbox" id="EPvalid" v-model="EPisCourse">
          <label for="EPvalid">Consider</label>
        </div>
      </div>
    </div>
    <div class="print-conf-container">
      <h3>Printing parameters</h3>
      <h5>How many tables do you want to print?</h5>
      <div>
        <div>
          <input type="radio" id="10" name="num-tables" value="10" v-model="nVariations">
          <label for="10">10</label>
        </div>
        <div>
          <input type="radio" id="100" name="num-tables" value="100" v-model="nVariations">
          <label for="100">100</label>
        </div>
        <div>
          <input type="radio" id="1000" name="num-tables" value="1000" v-model="nVariations">
          <label for="1000">1000</label>
        </div>
        <div>
          <input type="radio" id="10000" name="num-tables" value="10000" v-model="nVariations">
          <label for="10000">10000</label>
        </div>
      </div>
    </div>
    <div class="btn-container">
      <button class="btn btn-primary" @click="generateVariations()">Get {{nVariations}} VARIATIONS</button>
      <button class="btn" :class="variations.length === 0 ? 'disabled btn-secondary' : 'btn-success'" @click="transformJSONtoExcel">Export into EXCEL</button>
    </div>
    <div>
      <table class="mb-4" v-for="(v,i) in variations" :key="i">
        <tr>
          <td class="no-borders"></td>
          <td colspan="25">Variation {{i+1}}</td>
        </tr>
        <tr>
          <td class="no-borders"></td>
          <td colspan="5">6ème</td>
          <td colspan="6">5ème</td>
          <td colspan="6">4ème</td>
          <td colspan="5">3ème</td>
          <td colspan="2">EP</td>
          <td rowspan="2">TOTAL</td>
        </tr>
        <tr>
          <td class="no-borders"></td>
          <td>4.5h</td>
          <td>4.5h</td>
          <td>4.5h</td>
          <td>4.5h</td>
          <td>4.5h</td>
          <td>3h</td>
          <td>3h</td>
          <td>3h</td>
          <td>3h</td>
          <td>3h</td>
          <td>3h</td>
          <td>3h</td>
          <td>3h</td>
          <td>3h</td>
          <td>3h</td>
          <td>3h</td>
          <td>3h</td>
          <td>3.5h</td>
          <td>3.5h</td>
          <td>3.5h</td>
          <td>3.5h</td>
          <td>3.5h</td>
          <td>1h</td>
          <td>1h</td>
        </tr>
        <tr v-for="(p,j) in Object.keys(v)" :key="j">
          <td>{{p}}</td>
          <td v-for="(x,k) in transformVariationObjToArr(v[p].planning)" :key="k">
            {{x===0? "" : "X"}}
          </td>
          <td>
            {{v[p].hours}}
          </td>
        </tr>
        <tr>
          <td class="no-borders"></td>
          <td colspan="5">{{calculateIfCourseIsFull(v, 6, true)}}</td>
          <td colspan="6">{{calculateIfCourseIsFull(v, 5, true)}}</td>
          <td colspan="6">{{calculateIfCourseIsFull(v, 4, true)}}</td>
          <td colspan="5">{{calculateIfCourseIsFull(v, 3, true)}}</td>
          <td colspan="2">{{calculateIfCourseIsFull(v, "EP", true)}}</td>
          <td class="no-borders"></td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
const XLSX = require("xlsx");

export default {
  name: 'Home',
  data() {
    return {
      people: {
        Anne: Array.apply(null, Array(24)).map(function() {return 0}),
        Céline: Array.apply(null, Array(24)).map(function() {return 0}),
        Marjo: Array.apply(null, Array(24)).map(function(_, i) {
          if(i === 22) return 1;
          return 0;
        }),
        Edith: Array.apply(null, Array(24)).map(function() {return 0}),
        BMP: Array.apply(null, Array(24)).map(function(_, i) {
          if(i === 0) return 1;
          return 0;
        }),
      },
      conditions: {
        Anne: {
          minHours: 18,
          maxHours: 20,
          maxCourses: 3,
          requiredCourses: {
            6: false,
            5: false,
            4: false,
            3: false,
            EP: false,
          },
          forbiddenCourses: {
            6: false,
            5: false,
            4: true,
            3: false,
            EP: true,
          }
        },
        Céline: {
          minHours: 18,
          maxHours: 20,
          maxCourses: 3,
          requiredCourses: {
            6: false,
            5: false,
            4: false,
            3: false,
            EP: false,
          },
          forbiddenCourses: {
            6: false,
            5: false,
            4: false,
            3: false,
            EP: false,
          },
        },
        Marjo: {
          minHours: 15,
          maxHours: 15,
          maxCourses: 3,
          requiredCourses: {
            6: false,
            5: false,
            4: false,
            3: false,
            EP: true,
          },
          forbiddenCourses: {
            6: false,
            5: false,
            4: false,
            3: false,
            EP: false,
          },
        },
        Edith: {
          minHours: 18,
          maxHours: 21.5,
          maxCourses: 3,
          requiredCourses: {
            6: false,
            5: false,
            4: false,
            3: false,
            EP: false,
          },
          forbiddenCourses: {
            6: false,
            5: true,
            4: false,
            3: false,
            EP: true,
          },
        },
        BMP: {
          minHours: 7.5,
          maxHours: 7.5,
          maxCourses: 3,
          requiredCourses: {
            6: false,
            5: false,
            4: false,
            3: false,
            EP: false,
          },
          forbiddenCourses: {
            6: false,
            5: false,
            4: false,
            3: true,
            EP: true,
          },
        },
      },
      classes: {
        6: {
          qty: 6,
          hours: 4.5
        },
        5: {
          qty: 6,
          hours: 3
        },
        4: {
          qty: 6,
          hours: 3
        },
        3: {
          qty: 5,
          hours: 3.5
        },
        EP: {
          qty: 2,
          hours: 1
        },
      },
      EPisCourse: false,
      nVariations: 10,
      variations: [],
    }
  },
  methods: {
    selectUnselectSession(person, index) {
      this.people[person][index] = this.people[person][index] === 0 ? 1 : 0;
    },
    calculateTotalHoursForPerson(person) {
      return this.people[person].reduce((r,v,i) => {
        if(i < 5) return r + (v === 0 ? 0 : 4.5);
        if(i < 17) return r + (v === 0 ? 0 : 3);
        if(i < 22) return r + (v === 0 ? 0 : 3.5);
        if(i < 24) return r + (v === 0 ? 0 : 1);
      }, 0);
    },
    calculateTotalCoursesForPerson(person) {
      return this.people[person].reduce((r,v,i) => {
        if(i < 5 && v === 1) {
          return (r.includes(6) ? r : [...r, 6]);
        };
        if(i < 11 && v === 1) {
          return (r.includes(5) ? r : [...r, 5]);
        };
        if(i < 17 && v === 1) {
          return (r.includes(4) ? r : [...r, 4]);
        };
        if(i < 22 && v === 1) {
          return (r.includes(3) ? r : [...r, 3]);
        };
        if(i < 24 && v === 1 && this.EPisCourse) {
          return (r.includes("EP") ? r : [...r, "EP"]);
        };
        return r;
      }, []);
    },
    calculateDefaultPlanning(person) {
      const planning = {
        6: 0,
        5: 0,
        4: 0,
        3: 0,
        "EP": 0,
      };
      this.people[person].forEach((v,i) => {
        if(i < 5 && v === 1) {
          planning[6]++;
        } else if(i < 11 && v === 1) {
          planning[5]++;
        } else if(i < 17 && v === 1) {
          planning[4]++;
        } else if(i < 22 && v === 1) {
          planning[3]++;
        } else if(i < 24 && v === 1) {
          planning["EP"]++;
        };
      });
      return planning;
    },
    calculateIfCourseIsFull(variation, course, value=false) {
      const total = Object.values(variation).reduce((r,v) => {
        return r + v.planning[course];
      }, 0);
      if(value) {
        if(course == 6) return total*4.5;
        if([5,4].includes(course)) return total*3;
        if(course == 3) return total*3.5;
        if(course == "EP") return total;
      }
      if([6,5,4].includes(course)) return total<6;
      if(course == 3) return total<5;
      if(course == "EP") return total<2;
    },
    transformVariationObjToArr(obj) {
      return [6,5,4,3,"EP"].reduce((r,v) => {
        if(v === 6) return [ ...r, ...Array.apply(null, Array(5)).map(function(_, i) {
          if(obj[v] > i) return 1;
          return 0;
        })];
        if(v === 5) return [ ...r, ...Array.apply(null, Array(6)).map(function(_, i) {
          if(obj[v] > i) return 1;
          return 0;
        })];
        if(v === 4) return [ ...r, ...Array.apply(null, Array(6)).map(function(_, i) {
          if(obj[v] > i) return 1;
          return 0;
        })];
        if(v === 3) return [ ...r, ...Array.apply(null, Array(5)).map(function(_, i) {
          if(obj[v] > i) return 1;
          return 0;
        })];
        if(v === "EP") return [ ...r, ...Array.apply(null, Array(2)).map(function(_, i) {
          if(obj[v] > i) return 1;
          return 0;
        })];
      }, []);
    },

    increasDecreas(action, element, key) {
      if(action === "decreas") element[key]--;
      else element[key]++;
    },
    randomIntFromInterval(min, max) { 
      return Math.floor(Math.random() * (max - min + 1) + min)
    },
    getRandomCombination() {
      const names = ["Anne", "Céline", "Marjo", "Edith", "BMP"];
      const courses = [6, 5, 4, 3, "EP"];
      return [
        names[this.randomIntFromInterval(0, names.length-1)],
        courses[this.randomIntFromInterval(0, courses.length-1)]
      ];
    },

    generateVariation() {
      const variation = {};
      for(let p of ["Anne", "Céline", "Marjo", "Edith", "BMP"]) {
        variation[p] = {
          hours: this.calculateTotalHoursForPerson(p),
          courses: this.calculateTotalCoursesForPerson(p),
          planning: this.calculateDefaultPlanning(p)
        }
      };
      let countRandom = 0;
      while(countRandom < 10000) {
        const [p, c] = this.getRandomCombination();
        let cValue;
        switch(c) {
          case 6:
            cValue = 4.5;
            break;
          case 5:
            cValue = 3;
            break;
          case 4:
            cValue = 3;
            break;
          case 3:
            cValue = 3.5;
            break;
          case "EP":
            cValue = 1;
            break;
        }
        if(variation[p].courses.includes(c)) {
          if((variation[p].hours + cValue) <= this.conditions[p].maxHours &&
          this.calculateIfCourseIsFull(variation, c)) {
            variation[p].planning[c]++;
            variation[p].hours += cValue;
          }
        } else {
          if(this.conditions[p].forbiddenCourses[c] === false &&
          this.conditions[p].maxCourses > variation[p].courses.length &&
          (variation[p].hours + cValue) <= this.conditions[p].maxHours &&
          this.calculateIfCourseIsFull(variation, c)) {
            if(this.EPisCourse) variation[p].courses.push(c);
            variation[p].planning[c]++;
            variation[p].hours += cValue;
          }
        }
        countRandom++;
      }
      return variation;
    },
    generateVariations() {
      this.variations = [];
      let count = 0;
      while(count < 10000000000 && this.variations.length < this.nVariations) {
        const variation = this.generateVariation();
        if(!this.variations.includes(variation)) this.variations.push(variation);
        count++;
      }
    },

    transformJSONtoExcel() {
      const result = [];
      this.variations.forEach((v,i) => {
        // Add empty row
        result.push(
          Array.apply(null, Array(26)).reduce((r, _, j) => {
            r[`column ${j+1}`] = "";
            return r;
          }, {})
        );
        // Add Title
        result.push(
          Array.apply(null, Array(26)).reduce((r, _, j) => {
            if(j===0) r[`column ${j+1}`] = `VARIATION NUMBER ${i+1}`;
            else r[`column ${j+1}`] = "";
            return r;
          }, {})
        );
        // Add header
        result.push(
          Array.apply(null, Array(26)).reduce((r, _, j) => {
            if(j === 0) r[`column ${j+1}`] = "";
            else if(j < 6) r[`column ${j+1}`] = "6ème";
            else if(j < 12) r[`column ${j+1}`] = "5ème";
            else if(j < 18) r[`column ${j+1}`] = "4ème";
            else if(j < 23) r[`column ${j+1}`] = "3ème";
            else if(j < 25) r[`column ${j+1}`] = "EP";
            else r[`column ${j+1}`] = "TOTAL";
            return r;
          }, {})
        );
        // Add people planning
        Object.keys(v).forEach((p) => {
          const planning = this.transformVariationObjToArr(v[p].planning);
          result.push(
            Array.apply(null, Array(26)).reduce((r, _, j) => {
              if(j === 0) {
                r[`column ${j+1}`] = p;
              } else if(j < 6) {
                if(planning[j-1] === 1) r[`column ${j+1}`] = 4.5;
                else r[`column ${j+1}`] = "";
              } else if(j < 18) {
                if(planning[j-1] === 1) r[`column ${j+1}`] = 3;
                else r[`column ${j+1}`] = "";
              } else if(j < 23) {
                if(planning[j-1] === 1) r[`column ${j+1}`] = 3.5;
                else r[`column ${j+1}`] = "";
              } else if(j < 25) {
                if(planning[j-1] === 1) r[`column ${j+1}`] = 1;
                else r[`column ${j+1}`] = "";
              } else r[`column ${j+1}`] = v[p].hours;
              return r;
            }, {})
          );
        });
        // Add total hours x course
        result.push(
          Array.apply(null, Array(26)).reduce((r, _, j) => {
            if(j === 3) r[`column ${j+1}`] = this.calculateIfCourseIsFull(v, 6, true);
            else if(j === 8) r[`column ${j+1}`] = this.calculateIfCourseIsFull(v, 5, true);
            else if(j === 14) r[`column ${j+1}`] = this.calculateIfCourseIsFull(v, 4, true);
            else if(j === 20) r[`column ${j+1}`] = this.calculateIfCourseIsFull(v, 3, true);
            else if(j === 23) r[`column ${j+1}`] = this.calculateIfCourseIsFull(v, "EP", true);
            else r[`column ${j+1}`] = "";
            return r;
          }, {})
        );
        // Add empty row
        result.push(
          Array.apply(null, Array(26)).reduce((r, _, j) => {
            r[`column ${j+1}`] = "";
            return r;
          }, {})
        );
      });

      
      // console.log(result)
      const download_worksheet = XLSX.utils.json_to_sheet(result)
      const download_workbook = XLSX.utils.book_new();
      download_workbook.SheetNames.push("variations");
      download_workbook.Sheets["variations"] = download_worksheet;
      XLSX.writeFile(download_workbook, `${this.nVariations}-variations-${new Date().getTime()}.xlsx`);
    },
  },
  mounted() {
    // console.log(new Date().getTime());
    // console.log(this.transformVariationObjToArr(this.generateVariation()["Anne"].planning));
    // this.transformJSONtoExcel();
  },
}
</script>

